.stars {
  display: flex;
  &__star {
    height: 16px;
    width: 16px;
    margin-right: 4px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    &:last-child {
      margin-right: 0;
    }
  }
}

.stars--1 :first-child,
.stars--2 :nth-child(-n + 2),
.stars--3 :nth-child(-n + 3),
.stars--4 :nth-child(-n + 4),
.stars--5 :nth-child(n) {
  background-image: url(../images/star-active.svg);
}
